@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  font-family: "Pangolin", cursive;
}

.button {
  padding: 5px;
  box-shadow: 3px 3px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid;
}

.button:hover {
  color: #888;
}

.button:active {
  box-shadow: none;
  -webkit-transform: translate(3px, 3px);
          transform: translate(3px, 3px);
}

.contact-item-container{padding:15px;position:relative}.button-group{display:flex;flex-direction:column;width:70px;position:absolute;right:10px;top:5px}.contact-item-button{margin:5px 0}.contact-info__icon-and-name{display:flex;align-items:center}.contact-info__icon-and-name span{margin-left:5px;font-size:24px}.contact-info p{font-size:16px}

.popup-confirm-container{position:fixed;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0.2);display:flex;justify-content:center;align-items:center;z-index:1}.popup-confirm-inner{position:relative;padding:32px;width:60%;height:200px;max-width:640px;background-color:#fff;border:2px solid #000}

.popup-container{position:fixed;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0.2);display:flex;justify-content:center;align-items:center;z-index:1}.popup-form{position:relative;padding:32px;width:60%;height:500px;max-width:640px;background-color:#fff;border:2px solid #000}.popup-form h1{text-align:center}.form-button-group{position:absolute;bottom:32px;right:32px}.popup-inner{padding:24px}.fieldset,.fieldset-names,.fieldset-others{display:flex;flex-direction:column;padding:15px 0}.fieldset-names{width:49%}.fieldset-others{width:100%}.fieldset input,.fieldset-names input,.fieldset-others input{height:32px}.full-name{display:flex;justify-content:space-between}@media all and (max-width: 550px){.full-name{display:block}.fieldset-names{width:100%}}@media all and (max-width: 385px){.popup-form h1{font-size:24px}}

.contact-list-container{width:80%;max-width:1080px;margin:0 auto;display:flex;flex-flow:wrap;justify-content:center}.contact-list-item{border:2px solid #000;width:45%;margin:20px;min-width:350px}.header-container{position:relative;max-width:1080px;margin:0 auto;min-width:360px}.header-container h1{text-align:center;font-size:64px;margin-top:20px}.header-container .sort-control{position:absolute;top:50%;right:20%;-webkit-transform:translate(0, -50%);transform:translate(0, -50%);height:25px;width:25px;cursor:pointer;display:flex}.header-container .sort-control select{margin-right:5px}.header-container img{height:25px;width:25px;transition:0.2s}.header-container img.rotate{-webkit-transform:rotate(180deg);transform:rotate(180deg)}@media all and (max-width: 540px){.header-container h1{text-align:start}}

.nav-container{display:flex;justify-content:space-between;align-items:center;padding:15px;border:2px solid #000}.nav-container li{list-style:none;font-weight:400;margin:0 20px}.nav-container nav{width:80%}.nav-container ul{display:flex;width:100%}

